import React, { useEffect, useState } from 'react';
import logo from '@assets/img/logo.svg';
import { processProduct } from '@src/scripts/geminiProcess';

interface outputProps {
  productName: string;
  score: number;
  suggestedAlternative: string;
}

export default function Popup() {


  const [productData, setProductData] = useState<outputProps | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  // this will call AI
  useEffect(() => {
    const fetchData = async () => {
      try {
        const [tab] = await chrome.tabs.query({ active: true, currentWindow: true });

        if (!tab?.id || typeof tab.id !== "number") {
          throw new Error("No valid active tab ID");
        }

        const response = await chrome.tabs.sendMessage(tab.id, { type: "SCRAPE_PRODUCT" });

        const productTitle = response?.data?.title;
        if (!productTitle) throw new Error("No product title found");

        const result = await processProduct(productTitle);
        setProductData(result);
      } catch (err: any) {
        console.error("Error fetching product data:", err);
        setError("Failed to load product info.");
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, []);


  return (
    <div className="absolute top-0 left-0 right-0 bottom-0 text-center h-full p-3 bg-gray-800">
      <header className="flex flex-col items-center justify-center text-white">
        <img src={logo} className="h-36 pointer-events-none animate-spin-slow" alt="logo" />
        {loading ? (
          <p className="mt-4 text-gray-300">Processing product...</p>
        ) : error ? (
          <p className="text-red-400">{error}</p>
        ) : productData ? (
          <div className="mt-4 text-left text-white bg-gray-700 p-4 rounded shadow">
            <h2 className="text-xl font-bold mb-2">Product Analysis</h2>
            <p><strong>Product Name:</strong> {productData.productName}</p>
            <p><strong>Score:</strong> {productData.score}</p>
            <p><strong>Suggested Alternative:</strong> {productData.suggestedAlternative}</p>
          </div>
        ) : (
          <p>No data available.</p>
        )}
      </header>
    </div>
  );
}
